body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  min-width: 320px;
  font-family: 'Open Sans', sans-serif;
  font-size: 9px;
  line-height: 1.2;
  color: #000;
  background-color: #fff;
  overflow-x: hidden;
}
.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
button {
  font-family: inherit;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'TT Norms', sans-serif;
}
.flex {
  display: flex;
  flex-wrap: wrap;
}
.flex--start {
  justify-content: flex-start;
}
.flex--center {
  justify-content: center;
}
.flex--end {
  justify-content: flex-end;
}
.flex--between {
  justify-content: space-between;
}
.flex--around {
  justify-content: space-around;
}
.flex--top {
  align-items: flex-start;
}
.flex--middle {
  align-items: center;
}
.flex--bottom {
  align-items: flex-end;
}
.flex--column {
  flex-direction: column;
}
.flex--nowrap {
  flex-wrap: nowrap;
}
.flex--grow {
  flex-grow: 1;
}
.flex .w50 {
  width: calc(50% - 5px);
}
@media (min-width: 1024px) {
  .flex .w50 {
    width: calc(50% - 12px);
  }
}
.button-burger {
  padding: 0;
  margin: 0;
  border: none;
  background-color: #fff;
  display: block;
  width: 41px;
  height: 24px;
  margin-right: -3px;
  background-image: url("../images/icons/icon_burger.svg");
  background-repeat: no-repeat;
  background-size: 28px 14px;
  background-position: center;
}
.button-close {
  padding: 0;
  margin: 0;
  border: none;
  display: block;
  background-color: transparent;
  width: 40px;
  height: 40px;
  background-image: url("../images/icons/icon_close.svg");
  background-repeat: no-repeat;
  background-size: 25px;
  background-position: center;
}
.button {
  padding: 0;
  border: none;
  display: block;
  min-width: 120px;
  color: #fff;
  text-decoration: none;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  background-color: #7AC5AF;
  border: 2px solid #7AC5AF;
  border-radius: 100px;
  padding: 8px 10px;
  box-sizing: border-box;
  outline: none;
  transition: background 0.2s, border-color 0.2s, color 0.2s;
  cursor: pointer;
}
.button:hover,
.button:focus {
  color: #fff;
  background-color: #4EAE92;
  border-color: #4EAE92;
}
.button--white {
  color: #7AC5AF;
  background-color: #fff;
}
.button--white:hover,
.button--white:focus {
  color: #4EAE92;
  background-color: #fff;
}
@media (min-width: 1024px) {
  .button-burger {
    display: none;
  }
  .button {
    min-width: 140px;
    font-size: 18px;
    padding: 12px;
    padding-bottom: 13px;
    border-width: 3px;
  }
  .button:active {
    border-color: #2E8D71;
    background-color: #2E8D71;
  }
  .button--white:active {
    color: #2E8D71;
    background-color: #fff;
  }
}
@media (min-width: 1630px) {
  .button {
    min-width: 180px;
  }
}
.link {
  color: #7AC5AF;
  text-decoration: none;
  transition: color 0.2s;
}
.link:hover,
.link:active {
  color: #2E8D71;
}
.icon {
  transition: fill 0.2s;
}
.contacts {
  margin: 0;
  padding: 0;
  list-style: none;
  color: #A9A9A9;
}
.contacts__item {
  display: block;
}
.contacts__item:not(:last-child) {
  margin-bottom: 7px;
}
.contacts__link {
  padding: 0;
  border: none;
  background-color: transparent;
  color: inherit;
  font-size: 9px;
  text-decoration: none;
  white-space: nowrap;
  transition: color 0.2s;
}
.contacts__link:hover,
.contacts__link:focus {
  color: #4EAE92;
}
.contacts__link .icon {
  margin-right: 3px;
}
@media (min-width: 1024px) {
  .contacts__link {
    font-size: 14px;
  }
  .contacts__link .icon {
    transform: scale(1.5);
  }
}
.header {
  position: fixed;
  display: flex;
  align-items: center;
  z-index: 999;
  top: 0;
  right: 0;
  left: 0;
  min-height: 54px;
  background-color: #fff;
  border-bottom: 1px solid #C0C0C0;
  padding: 6px 13px;
  box-sizing: border-box;
}
.header__logo-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header__lang {
  position: absolute;
  right: 10px;
  bottom: 5px;
}
.header__nav {
  position: absolute;
  top: 4px;
  right: 7px;
}
@media (min-width: 1024px) {
  .header {
    min-height: 110px;
    background-color: transparent;
    border: none;
    padding-left: 170px;
    padding-right: 40px;
  }
  .header::before {
    content: "";
    position: absolute;
    display: block;
    opacity: 0;
    z-index: -1;
    width: 100%;
    height: calc(100% + 30px);
    left: 0;
    top: 0;
    transition: opacity 0.4s;
    background-image: linear-gradient(to bottom, #fff 50%, rgba(255, 255, 255, 0) 100%);
  }
  .header--inner::before {
    opacity: 1;
  }
  .header.active::before {
    opacity: 1;
  }
  .header__logo-box {
    left: 35px;
    transform: translate(0, -50%);
  }
  .header__logo {
    width: 100px;
  }
  .header__nav {
    order: 1;
    position: relative;
    top: auto;
    right: auto;
  }
  .header__contacts {
    order: 2;
    margin-left: auto;
  }
  .header__contacts .icon {
    transform: scale(3.5) translate(-4px, -1px);
  }
  .header__contacts .contacts__link {
    font-size: 18px;
  }
  .header__contacts .contacts__item {
    margin-bottom: 0;
  }
  .header__lang {
    position: relative;
    right: auto;
    bottom: auto;
    order: 5;
    margin-left: 65px;
  }
}
@media (min-width: 1630px) {
  .header {
    padding-left: 235px;
  }
  .header__logo {
    width: 140px;
  }
}
.lang {
  color: #7AC5AF;
}
.lang__button {
  padding: 0;
  border: none;
  color: inherit;
  font-weight: 500;
  font-size: 13px;
  text-decoration: none;
  transition: color 0.2s;
}
.lang__button:hover,
.lang__button:focus {
  color: #4EAE92;
}
@media (min-width: 1024px) {
  .lang__button {
    font-size: 20px;
  }
}
.nav {
  color: #4c4c4c;
}
.nav .button-burger.active + .nav__list {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.2s, visibility 0s 0s;
}
.nav__list {
  opacity: 0;
  margin: 0;
  list-style: none;
  position: fixed;
  left: 0;
  right: 0;
  top: 54px;
  padding: 7px 13px;
  background-color: #fff;
  transition: opacity 0.2s, visibility 0s 0.2s;
  visibility: hidden;
}
.nav__item {
  margin-bottom: 7px;
}
.nav__link {
  position: relative;
  display: block;
  width: 100%;
  color: inherit;
  text-decoration: none;
  font-size: 20px;
  text-align: right;
}
.nav__link:hover,
.nav__link:focus {
  color: #4EAE92;
}
.nav__link::before {
  content: "";
  position: absolute;
  display: block;
  width: 0;
  height: 2px;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
  background-color: #343434;
}
.nav__link.active {
  color: #4EAE92;
}
.nav__link.active::before {
  width: 92px;
  background-color: #4EAE92;
}
@media (min-width: 1024px) {
  .nav__list {
    display: flex;
    position: relative;
    opacity: 1;
    visibility: visible;
    left: auto;
    right: auto;
    top: auto;
    padding: 0;
    background-color: transparent;
  }
  .nav__item {
    margin-bottom: 0;
    margin-right: 30px;
  }
  .nav__link {
    font-size: 18px;
    font-weight: 500;
    transition: color 0.2s;
  }
  .nav__link:hover::before,
  .nav__link:focus::before {
    width: 44px;
    background-color: #4EAE92;
  }
  .nav__link::before {
    width: 0;
    top: auto;
    bottom: -5px;
    left: 0;
    height: 2px;
    transition: width 0.2s, bacground-color 0.2s;
  }
  .nav__link.active::before {
    width: 44px;
  }
}
@media (min-width: 1630px) {
  .nav__item {
    margin-right: 60px;
  }
}
.main {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  padding-top: 54px;
  box-sizing: border-box;
}
.main--inner {
  height: auto;
}
.main--inner .main__title {
  position: relative;
  margin: 0;
  width: 100%;
  color: #2E8D71;
  font-size: 20px;
  text-align: right;
  font-weight: 400;
  padding: 0 13px;
  margin-top: 6px;
  box-sizing: border-box;
}
.main--inner .main__title::before {
  content: "";
  position: absolute;
  display: block;
  width: 92px;
  height: 2px;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
  background-color: #2E8D71;
}
.main__block {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  background-color: #fff;
  transition: all 0.4s ease-in-out;
}
.main__block::after {
  content: "";
  position: absolute;
  z-index: -1;
  opacity: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: opacity 0.4s 0.4s;
}
.main__block--first {
  clip-path: polygon(0 0, 100% 0, 100% 90%, 0 100%);
  margin-bottom: -5%;
}
.main__block--first::after {
  background-image: url("../images/pictures/bg_main-second.jpg");
}
.main__block--second {
  clip-path: polygon(0 10%, 100% 0, 100% 100%, 0 100%);
  margin-top: -5%;
}
.main__block--second::after {
  background-image: url("../images/pictures/bg_main-first.jpg");
}
.main__block--second::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  transform: rotate(-5deg);
  transform-origin: top right;
}
.main__block.active {
  clip-path: none;
  flex-grow: 0;
  position: absolute;
  z-index: 3;
  top: 54px;
  left: 0;
  height: 78px;
  width: 100%;
  margin: 0;
}
.main__block.active::after {
  opacity: 0;
  transition: opacity 0s 0s;
}
.main__block.active::before {
  display: none;
}
.main__block.active .main__title {
  margin-bottom: 2px;
}
.main__block.active .main__button-box {
  display: flex;
}
.main__block.active .main__button-box .button {
  min-width: 95px;
  margin: 0;
  padding: 5px;
}
.main__block.active .main__button-box .button:not(:last-child) {
  margin-right: 10px;
}
.main__title {
  margin: 0;
  font-size: 30px;
  color: #343434;
  text-align: center;
  margin-bottom: 15px;
}
.main__button-box .button {
  margin: 0 auto;
}
.main__button-box .button:not(:last-child) {
  margin-bottom: 7px;
}
.main__block-button-arrow {
  display: none;
}
.main__section-title {
  margin: 0;
  font-size: 40px;
  color: #343434;
  margin-bottom: 15px;
}
@media (min-width: 1024px) {
  .main {
    display: flex;
    justify-content: space-between;
    padding: 0;
    flex-direction: row;
    background-image: url("../images/pictures/bg_main.jpg");
    background-size: cover;
    background-position: center;
  }
  .main::before {
    content: "";
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.25);
  }
  .main--inner {
    position: relative;
    flex-direction: column;
    background: #fff;
    padding-top: 110px;
  }
  .main--inner::before {
    display: none;
  }
  .main--inner .main__title {
    position: absolute;
    font-size: 250px;
    font-weight: 700;
    top: -600px;
    left: 300px;
    transform: rotate(90deg);
    transform-origin: top left;
    opacity: 0.3;
  }
  .main--inner .main__title::before {
    display: none;
  }
  .main--inner .section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 100px;
    padding-top: 30px;
  }
  .main--inner .news__list {
    display: block;
    justify-content: flex-start;
    margin: 0 auto;
  }
  .main__title-bg {
    user-select: none;
    margin: 0;
    position: absolute;
    display: flex;
    flex-direction: column;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgba(255, 255, 255, 0.5);
    font-size: 37vh;
    line-height: 0.8;
    font-weight: 500;
    text-transform: uppercase;
  }
  .main__block {
    position: relative;
    width: 35%;
    min-width: 500px;
    margin: 0;
    z-index: 3;
    padding: 0 64px;
    box-sizing: border-box;
  }
  .main__block::after {
    opacity: 0;
  }
  .main__block--first {
    clip-path: polygon(0 0, 100% 0, 80% 100%, 0 100%);
    padding-right: 100px;
  }
  .main__block--first.active {
    clip-path: polygon(0 0, 100% 0, 80% 100%, 0 100%);
    transform: translateX(-70%);
  }
  .main__block--first .main__title,
  .main__block--first .main__block-text {
    text-align: left;
  }
  .main__block--first .main__button-box {
    justify-content: flex-start;
  }
  .main__block--first .main__block-button-arrow {
    right: 40px;
    background-image: url("../images/icons/icon_arrow-right.svg");
  }
  .main__block--second {
    clip-path: polygon(20% 0, 100% 0, 100% 100%, 0 100%);
    padding-left: 100px;
  }
  .main__block--second.active {
    clip-path: polygon(20% 0, 100% 0, 100% 100%, 0 100%);
    transform: translateX(70%);
  }
  .main__block--second .main__title,
  .main__block--second .main__block-text {
    text-align: right;
  }
  .main__block--second .main__button-box {
    justify-content: flex-end;
  }
  .main__block--second .main__block-button-arrow {
    left: 40px;
    order: -1;
    background-image: url("../images/icons/icon_arrow-left.svg");
  }
  .main__block.active {
    position: relative;
    top: auto;
    left: auto;
    height: 100%;
    width: 33%;
  }
  .main__block.active::after {
    opacity: 1;
  }
  .main__block.active .main__button-box .button {
    min-width: 140px;
    padding: 12px;
    padding-bottom: 13px;
  }
  .main__block.active .main__button-box .button:not(:last-child) {
    margin-right: 40px;
  }
  .main__block.active .main__title {
    margin-bottom: 20px;
  }
  .main__block.active .main__block-wrapper {
    opacity: 1;
  }
  .main__block.active .main__block-button-arrow {
    opacity: 1;
    visibility: visible;
  }
  .main__block.active .main__block-button-arrow:hover,
  .main__block.active .main__block-button-arrow:focus {
    opacity: 0.8;
  }
  .main__title {
    display: block;
    width: 100%;
    font-size: 50px;
    font-weight: 600;
    margin-bottom: 20px;
  }
  .main__block-text {
    margin: 0;
    color: #343434;
    font-size: 14px;
    margin-bottom: 50px;
  }
  .main__button-box {
    display: flex;
  }
  .main__button-box .button {
    margin: 0;
  }
  .main__button-box .button:not(:last-child) {
    margin: 0;
    margin-right: 40px;
  }
  .main__block-button-arrow {
    opacity: 0;
    visibility: hidden;
    padding: 0;
    border: none;
    background-color: transparent;
    position: absolute;
    display: block;
    width: 80px;
    min-width: 80px;
    height: 80px;
    background-repeat: no-repeat;
    background-size: 50px;
    background-position: center;
    outline: none;
    transition: opacity 0.2s;
    margin-top: -5px;
    cursor: pointer;
  }
  .main__curator-box {
    position: absolute;
    opacity: 0;
    top: 20%;
    max-width: 80%;
    transition: opacity 0.3s;
  }
}
@media (min-width: 1630px) {
  .main__title {
    font-size: 50px;
  }
  .main__block.active .main__button-box .button {
    min-width: 180px;
  }
  .main__block.active .main__block-wrapper {
    opacity: 1;
  }
  .main__block.active .main__curator-box {
    opacity: 1;
  }
  .main__block--first.active,
  .main__block--second.active {
    transform: none;
  }
  .main__block--first .main__block-button-arrow,
  .main__block--second .main__block-button-arrow {
    position: relative;
    right: auto;
    left: auto;
  }
  .main__block-button-arrow {
    background-size: 35px;
  }
  .main__title-box {
    position: relative;
    display: flex;
  }
}
.post__image-box {
  width: 100%;
  height: 144px;
}
.post__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.post__content {
  padding: 7px 16px;
}
.post__title {
  margin: 0;
  color: #343434;
  font-size: 18px;
  margin-bottom: 7px;
}
.post__date {
  color: #2E8D71;
  font-size: 12px;
  margin-bottom: 12px;
}
.post__text {
  margin: 0;
  color: #000;
  font-size: 12px;
}
.contact-page__map-box {
  width: 100%;
  height: 144px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  margin-top: 7px;
}
.contact-page__content {
  padding: 15px 19px;
}
.contact-page__box {
  margin-bottom: 8px;
}
.contact-page__name {
  display: block;
  color: #343434;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 2px;
}
.contact-page__link {
  display: block;
  color: #7AC5AF;
  font-size: 14px;
  font-weight: 300;
}
