.only-desktop {
    display: none;
}

.inner {

    & .footer {
        margin: 0;
    }

    &__title {
        display: none;
    }
}

.hidden {
    display: none !important;
}

@media (min-width: @laptop-width) {
    .only-mobile {
        display: none;
    }

    .only-desktop {
        display: inherit;
    }

    .inner {
        position: relative;
        display: flex;
        flex-direction: column;

        min-height: 100vh;

        padding-top: 60px;

        box-sizing: border-box;

        & .footer {
            margin: 0;
            margin-top: auto;
        }

        &__title {
            display: block;
            margin: 0;
            position: absolute;
            z-index: -1;

            opacity: 0.3;

            color: @color-green;
            font-size: 100px;
            font-weight: 700;
            text-transform: uppercase;

            transform: rotate(90deg) translate(50%, 50%);
            top: 42px;
            left: 120px;

            &--contacts {
                left: -170px;
            }
        }
    }
}

@media (min-width: @desktop-width) {
    .inner {
        &__title {
            left: 185px;

            &--contacts {
                left: -165px;
            }
        }
    }
}