@media (min-width: @desktop-width) {
    .curator {
        display: flex !important;
        align-items: center;

        &__avatar-wrapper {
            width: 60px;
            min-width: 60px;
            height: 60px;

            border-radius: 50%;
            overflow: hidden;

            margin-right: 10px;
        }

        &__avatar {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &__name {
            position: relative;
            margin: 0;
            display: block;

            font-size: 16px;
            color: @color-black;
            font-weight: 700;

            padding-right: 50px;
            margin-right: 50px;

            &::before {
                content: "";
                position: absolute;
                display: block;

                top: 0;
                right: 0;
                bottom: 0;

                width: 1px;

                background-color: @color-green;
            }

            & span {
                display: block;

                &:first-child {
                    font-weight: 400;
                    font-size: 14px;
                }
            }
        }

        &__link {
            display: block;
            font-weight: 600;
            font-size: 16px;
            color: #8ab3a7;
            margin-bottom: 10px;
        }

        &__text {
            margin: 0;
            font-size: 10px;
            color: #7a7a7a;
        }
    }
}