.lang {
    color: @color-green-light;

    &__button {
        padding: 0;
        border: none;

        color: inherit;
        font-weight: 500;
        font-size: 13px;
        text-decoration: none;

        transition: color 0.2s;

        &:hover,
        &:focus {
            color: @color-green;
        }
    }
}

@media (min-width: @laptop-width) {
    .lang {
        &__button {
            font-size: 20px;
        }
    }
}