.tarif {
    &__list {
        padding: 0;
        margin: 0;
        list-style: none;

        margin: 0 auto;
    }

    &__item {
        padding: 12px 19px;
        padding-bottom: 18px;

        width: 218px;

        margin: 0 8px;
        margin-bottom: 8px;
        box-sizing: border-box;

        background: #FFFFFF;
        box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.2);

        & .button {
            margin: 0 auto;
        }
    }

    &__item-header {
        padding-bottom: 12px;
        margin-bottom: 12px;
        border-bottom: 1px solid #DCDCDC;
    }

    &__item-title {
        margin: 0;

        color: @color-black;
        font-size: 18px;
        text-align: center;

        margin-bottom: 18px;
    }

    &__price {
        margin: 0;
        display: block;

        color: @color-cyan;
        font-size: 26px;
        text-align: center;
        font-weight: 600;
    }

    &__text {
        margin: 0;

        font-size: 12px;
        text-align: center;
    }

    &__item-list {
        margin: 0;
        padding: 0;
        list-style: none;
        padding-left: 16px;

        margin-bottom: 25px;
    }

    &__item-item {
        font-size: 12px;

        &::before {
            content: "-";
            color: @color-green-dark;
            font-weight: 700;
        }
    }
}

@media (min-width: @laptop-width) {
    .tarif {

        &__list {
            margin: 0 -40px;
        }

        &__item {
            position: relative;

            display: flex;
            flex-direction: column;
            padding: 20px 15px;
            padding-bottom: 34px;

            height: 385px;

            transition: box-shadow 0.2s;

            margin: 0 40px;
            margin-bottom: 50px;

            overflow: hidden;

            &::before {
                content: "";
                position: absolute;
                display: block;

                width: 100%;
                height: 4px;

                top: 0;
                left: 0;
                transform: translateY(-4px);

                background-color: @color-green-dark;

                transition: transform 0.2s;
            }

            &:hover {
                box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

                &::before {
                    transform: translateY(0);
                }

                & .tarif {
                    &__item-title {
                        color: @color-green-dark;
                    }
                }
            }

            & .button {

                &:hover,
                &:active {
                    border-color: @color-green-dark;
                    background-color: @color-green-dark;
                }
            }
        }

        &__item-title {
            font-size: 20px;

            margin-bottom: 20px;

            transition: color 0.2s;
        }

        &__item-header {
            padding-bottom: 20px;
            margin-bottom: 20px;
        }

        &__price-box {
            display: flex;
            align-items: flex-end;
            justify-content: center;
        }

        &__price {
            font-size: 30px;
        }

        &__text {
            color: #848484;
            padding-bottom: 5px;

            &::before {
                content: "/"
            }
        }

        &__item-list {
            margin-bottom: auto;
        }

        & .section__bg-text {
            right: auto;
            left: 60px;
            bottom: auto;
            top: 70%;
            transform: rotate(-90deg);

            transform-origin: bottom left;
        }
    }
}