.contacts {
    margin: 0;
    padding: 0;
    list-style: none;

    color: @color-gray;

    &__item {
        display: block;

        &:not(:last-child) {
            margin-bottom: 7px;
        }
    }

    &__link {
        padding: 0;
        border: none;
        background-color: transparent;

        color: inherit;
        font-size: 9px;
        text-decoration: none;
        white-space: nowrap;

        transition: color 0.2s;

        &:hover,
        &:focus {
            color: @color-green;
        }

        & .icon {
            margin-right: 3px;
        }
    }
}

@media (min-width: @laptop-width) {
    .contacts {
        &__link {
            font-size: 14px;

            & .icon {
                transform: scale(1.5);
            }
        }
    }
}