.caworking {
    &__block {
        margin-bottom: 16px;
    }
}

@media (min-width: @laptop-width) {
    .caworking {
        &__tab-list {
            padding: 0;
            margin: 0;
            list-style: none;

            display: flex !important;

            margin-bottom: 28px;
        }

        &__tab-item {
            font-size: 20px;
            color: #a3a3a3;

            &:not(:last-child) {
                margin-right: 6px;

                &::after {
                    content: "/";
                    padding-left: 5px;
                }
            }
        }

        &__tab-button {
            padding: 0;
            border: none;
            background-color: transparent;

            font-size: inherit;
            color: inherit;

            cursor: pointer;

            transition: color 0.2s;

            outline: none;

            &:hover,
            &:focus,
            &.active {
                color: @color-green-dark;
            }
        }

        &__block {
            display: none;

            &.active {
                display: block;
            }
        }
    }
}