html {
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'TT Norms', sans-serif;
  font-size: 15px;
  position: relative;
  overflow-x: hidden !important;
}

.phone-number {
  text-align: left;
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
  margin-right: -30px;
}

.slider_item1 p {
  margin-top: 30px;
}

.clearfix:after {
  content: '';
  display: table;
  width: 100%;
}

/*  * {
    -moz-transition: all 0.3s ease;
    -webkit-transition-property: all;
    -webkit-transition-duration: 0.3s;
    -webkit-transition-timing-function: ease;
  }
*/
ul,
li {
  display: inline-block;
  padding: 0px;
  margin: 0px;
}

a,
a:hover,
a:active,
a:focus,
button,
input,
button:hover,
button:active,
button:focus,
input:focus,
input:active {
  text-decoration: none;
  outline: none;
}


/* <MENU css> */
nav.menu__fix {
  height: 70px;
  display: flex;
  justify-content: center;
  margin: 0;
  float: left;
}

.row--width {
  max-width: 1570px;
  margin: 0 auto;

}

.gallery-left {
  padding-left: 70px;
}

.active__mode {
  position: relative;
  display: inline-block;

}

.container {
  max-width: 1550px;
}

.left-column-blog {
  left: -4px;
}

.map-popup {
  position: fixed;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  max-width: 640px;
  max-height: 360px;
  display: none;
  z-index: 5;
}

.map-popup-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  background: rgba(0, 0, 0, 0.5);
  display: none;
}

.map-wrap iframe {
  width: 700px;
  height: 500px;


}

.map-popup-js {
  cursor: pointer;
}


@media (min-width: 992px) {

  .logo-head {
    margin-top: 7px;

  }

  .menu__item {
    margin-left: 15px;
  }

  .col-lg-4:hover .title-prev-news {
    color: #2e8d71;
    transition: .3s ease-out;
  }

  .col-lg-4 {
    cursor: pointer;
    overflow: hidden;
  }

  .col-lg-4:hover img {
    transform: scale(1.05);
    transition: 0.3s ease-out;
  }

  .lasted-news-block {
    max-width: 169px;
    max-height: 169px;
    overflow: hidden;
  }
}

a.menu__item__link:hover::before {
  content: '';
  position: absolute;
  height: 4px;
  width: 40px;
  display: block;
  background: #2e8d71;
  left: 15px;
  bottom: 20px;
}


.header__menu {
  line-height: 70px;
}

.menu__fix a {
  color: #4c4c4c;
  font-size: 18px;
  transition: color 0.3s ease;
}

.menu__fix a:hover {
  transition: color 0.3s ease;
  color: #2e8d71;
}

.header_menu__mob {
  border: none;
  outline: none;
  background: none;
  color: #fffde1;
  display: none;
}

@media screen and (max-width: 991px) {
  .header__menu {
    justify-content: center;
    display: none;
    width: 100%;
    background: #fff;
    padding: 0 5vw 5vw 5vw;
    left: 0;
    flex-direction: column;
    height: auto;
    position: fixed;
  }

  .active__mode {
    display: inherit;
  }

  .phone-number {
    text-align: left;
    margin-top: 20px;
    display: block;
  }

  .header__menu .menu____item {
    padding: 20px 0 0;
  }

  .header__menu .menu____item a {
    font-size: 20px;
  }

  .header_menu__mob {
    display: block;
    font-size: 32px;
    color: #000;
    background: #fff;
    margin-left: 20px;
  }

}

@media screen and (max-width:1680px) {
  .left--blog {
    display: flex;
    justify-content: flex-end;
    flex: 0 0 15.5%;
  }

  .phone-number {
    margin-right: 0px;
  }

  .language {
    margin-left: 0px !important;
  }
}

@media screen and (max-width:1540px) {
  .move-content--left {
    padding-left: 134px;
  }
}

@media screen and (max-width:1440px) {
  .left--blog {
    display: flex;
    justify-content: flex-end;
    flex: 0 0 15.5%;
  }

  .move-content--left {
    padding-left: 134px;
  }
}

@media screen and (max-width:1365px) {
  .row--center {
    padding-left: 10px;
  }

  .left-column-blog {
    left: -5px;
  }
}

@media (min-width: 991px) {
  .menu__fix {
    display: block;
  }

}

/* </MENU css> */

/* <Language css> */
.language {
  position: relative;
  cursor: pointer;
  display: inline-block;
  margin-top: 10px;
  padding-bottom: 15px;
  width: 90px;
  margin-left: 30px;
}

.language__btn {
  color: #7ac5af;
  font-size: 1.600rem;
  padding: 0 0 0 11px;
  border-radius: 45px;
  margin-top: 5px;
}

.language__btn:hover {
  color: #2E8D71;
  transition: all .3s ease-out;
}

.language__img {
  background: url(../img/main/language.png)no-repeat center center / contain;
  width: 9px;
  height: 30px;
  position: absolute;
  top: 9px;
  right: 13px;
}

.language__selection {
  display: none;
  text-align: center;
  position: absolute;
  background: rgb(46, 141, 113);
  border-radius: 7px;
  padding: 1px 0;
  bottom: -22px;
  width: 83px;
}

.language__selection a:first-child {
  border-bottom: 1px solid #fff;
}

.language__selection a {
  color: #fff;
  font-size: 18px;
  display: block;
}

.language__selection a:last-child {
  padding: 0 0 4px 0;
}

.language__selection a:hover {
  background: #fff;
  color: #2e8d71;
}

.language:hover .language__selection {
  display: block;
}

@media screen and (max-width: 991px) {
  .language {
    display: none;
  }

  .move-content--left {
    padding-left: 0px;
  }
}

a.menu__item__link:hover::before {
  content: '';
  position: absolute;
  height: 4px;
  width: 40px;
  display: block;
  background: #2e8d71;
  left: 15px;
  bottom: 20px;
}

/* </Language css> */

/* <Phone number css> */
.phone-number a {
  text-decoration: none;
  font-size: 18px;
  color: #838383;
  vertical-align: middle;

}

.phone-number a:hover {
  text-decoration: none;
  color: #2E8D71;
}

.phone-number a img {
  margin-right: 15px;
  vertical-align: middle;
}

/* </Phone number css> */
.small-mobile-footer {
  position: relative;
}

/* <Adress number css> */

.adress-number {
  text-align: left;
  margin-top: 5px;
  display: none;
}

.adress-number span#adress__head {
  font-size: 18px;
  color: #838383;
  vertical-align: middle;
}

@media (min-width: 577px) and (max-width: 651px) {
  .adress-number span#adress__head {
    font-size: 14px;
  }
}

.adress-number img {
  margin-right: 15px;
  vertical-align: middle;
}

/* </Adress number css> */

/* <Content blog category css> */

section#content-blog-category {
  margin-top: 80px;
}

.left-column-blog {
  position: relative;
}

.left-column-blog::before {
  content: '';
  position: absolute;
  height: 100%;
  width: 1px;
  display: block;
  background: #e1f1ec;
  left: 0;
  z-index: -1;
}

.middle-container {
  border: 1px solid #e4e4e4;
  width: 100%;
  position: relative;
  margin-bottom: 40px;
  cursor: pointer;
  background-color: #fff;
}

.middle-container:hover:after {
  content: '';
  position: absolute;
  height: 6px;
  width: 100%;
  display: block;
  background: #2E8D71;
  left: 0;
  top: 0;
}

.img-blog-cat {
  position: relative;
  width: 100%;
}

.img-blog-cat img {
  width: 100%;
}

.text-data {
  position: absolute;
  top: 11px;
  right: 6px;
  color: #ffffff;
  font-family: "TT Norms";
  font-size: 12px;
  font-weight: 700;
}

.title-blog-cat {
  margin-top: 15px;
  margin-left: 10px;
  font-size: 18px;
  font-weight: bold;
  color: #7ac5af;
}

.title-blog-cat p,
.text-blog-cat p {
  margin-bottom: 5px;
}

.text-blog-cat {
  padding: 5px 10px;
  font-size: 12px;
  color: #676767;
}

.button-blog-cat {
  text-align: center;
}

.button-blog-cat a {
  margin: 28px 15%;
  padding: 5px 5px;
  background-color: rgb(122, 197, 175);
  color: rgb(255, 255, 255);
  font-size: 18px;
  font-weight: bold;
  border-radius: 50px;
  display: inline-block;
  width: 70%;
}

@media screen and (max-width: 991px) {
  .left-column-blog {
    display: none;
  }

}

a.menu__item__link:active:before {
  display: none;
}

/* </Content blog category css> */

.h1-title-page {
  display: none;
}

/* <Footer blog category css> */

footer#main-footer-all {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  padding: 20px 0;
}

.footer-links a {
  display: block;
  text-align: right;
  line-height: 1.5;
  color: #8f8f8f;
  font-size: 14px;
}

.footer-links a:hover {
  color: #2E8D71;
  transition: .3s ease-out;
}

.footer-social a {
  display: inline-block;
  float: right;
}

.footer-social a:last-child {
  margin-right: 10px;
}

.contacts-footer {
  float: left;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.2;
}

.contacts-footer a,
.contacts-footer span {
  color: #8f8f8f;
}

.copy-detelegraph {
  margin-left: 17px;
  margin-top: 10px;
}

.envelo a img,
.phone-footer a img,
.map-you img,
.copy-detelegraph span {
  margin-right: 10px;
}

.phone-footer {
  margin: 10px 0;
}

.buttons-footer a,
.link-footer a {
  font-size: 10px;
}

.button-footer-call {}

.block-width {
  width: 340px;
}

.button-footer-call a {
  margin: 15px 0;
  padding: 10px 10px;
  background-color: rgb(122, 197, 175);
  color: rgb(255, 255, 255);
  border-radius: 50px;
  display: inline-block;
  border: 1px solid rgb(122, 197, 175);
}

.link-footer {
  margin-left: 18px;
  width: 50%;
  text-align: left;
}

.link-footer a {
  color: #7ac5af;
  font-size: 12px;
  letter-spacing: 1.5px;
  text-decoration: underline;
  display: block;
}

.logo-footer {
  margin-top: 20px;
}

/* </Footer blog category css> */
