body {
    margin: 0;
    padding: 0;

    display: flex;
    flex-direction: column;

    width: 100vw;
    min-height: 100vh;
    min-width: 320px;

    font-family: 'Open Sans', sans-serif;
    font-size: 9px;
    line-height: 1.2;
    color: #000;

    background-color: #fff;

    overflow-x: hidden;
}

.visually-hidden {
    position: absolute !important;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    padding: 0 !important;
    border: 0 !important;
    height: 1px !important;
    width: 1px !important;
    overflow: hidden;
}

button {
    font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'TT Norms', sans-serif;
}