.header {
    position: fixed;
    display: flex;
    align-items: center;
    z-index: 999;

    top: 0;
    right: 0;
    left: 0;

    min-height: 54px;

    background-color: #fff;
    border-bottom: 1px solid #C0C0C0;

    padding: 6px 13px;
    box-sizing: border-box;

    &__logo-box {
        position: absolute;

        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &__lang {
        position: absolute;

        right: 10px;
        bottom: 5px;
    }

    &__nav {
        position: absolute;

        top: 4px;
        right: 7px;
    }
}

@media (min-width: @laptop-width) {
    .header {
        min-height: 110px;

        background-color: transparent;

        border: none;

        padding-left: 170px;
        padding-right: 40px;

        &::before {
            content: "";
            position: absolute;
            display: block;
            opacity: 0;
            z-index: -1;

            width: 100%;
            height: calc(100% + 30px);

            left: 0;
            top: 0;

            transition: opacity 0.4s;

            background-image: linear-gradient(to bottom, #fff 50%, rgba(255, 255, 255, 0) 100%);
        }

        &--inner {
            &::before {
                opacity: 1;
            }
        }

        &.active {
            &::before {
                opacity: 1;
            }
        }

        &__logo-box {
            left: 35px;
            transform: translate(0, -50%)
        }

        &__logo {
            width: 100px;
        }

        &__nav {
            order: 1;
            position: relative;

            top: auto;
            right: auto;
        }

        &__contacts {
            order: 2;

            margin-left: auto;

            & .icon {
                transform: scale(3.5) translate(-4px, -1px);
            }

            & .contacts__link {
                font-size: 18px;
            }

            & .contacts__item {
                margin-bottom: 0;
            }
        }

        &__lang {
            position: relative;
            right: auto;
            bottom: auto;
            order: 5;

            margin-left: 65px;
        }
    }
}

@media (min-width: @desktop-width) {
    .header {
        padding-left: 235px;

        &__logo {
            width: 140px;
        }
    }
}