.tab {
    position: fixed;
    z-index: 9;

    top: 132px;
    left: 0;
    right: 0;

    height: calc(100vh - 132px);

    background-color: #fff;

    transform: translateY(100%);

    transition: transform 0.4s ease-in-out;

    padding-top: 25px;

    box-sizing: border-box;

    &.active {
        animation: up 0.4s ease-in-out forwards;

        &.removing {
            animation: down 0.4s ease-in-out forwards;
        }
    }

    &__box {
        height: 100%;

        overflow: hidden;
        overflow-y: auto;

        box-sizing: border-box;

        display: none;

        &.active {
            display: block;
        }
    }

    &__nav {
        position: absolute;
        z-index: 9;

        top: 0;
        left: 0;
        right: 0;

        height: 23px;

        overflow-x: auto;
        width: 100%;

        border: 1px solid @color-gray-light;
        border-left: none;
        border-right: none;

        background-color: #fff;
    }

    &__nav-list {
        padding: 0;
        margin: 0;
        list-style: none;
        display: flex;
        flex-wrap: nowrap;
    }

    &__nav-link {
        display: block;

        color: #000;
        font-size: 11px;
        text-decoration: none;
        font-weight: 300;
        white-space: nowrap;

        padding: 5px 6px;

        &.active {
            color: @color-green-light;
            font-weight: 600;
        }
    }

    @keyframes up {
        from {
            transform: translateY(100%)
        }

        to {
            transform: translateY(0)
        }
    }

    @keyframes down {
        from {
            transform: translateY(0)
        }

        to {
            transform: translateY(100%)
        }
    }
}

@media (min-width: @laptop-width) {
    .tab {
        padding-top: 0;

        transform: none;
        transition: none;

        top: auto;

        height: 100vh;
        width: 95vw;

        display: none;

        &.active {
            top: 0;
            height: 100vh;
            display: block;
        }


        &.to-left {
            animation: toLeft 0.4s ease-in-out forwards;

            clip-path: polygon(8% 0, 100% 0, 100% 100%, 0 100%);

            &.removing {
                animation: removingnLeft 0.4s ease-in-out forwards;
            }
        }

        &.to-right {
            animation: toRight 0.4s ease-in-out forwards;

            clip-path: polygon(0 0, 100% 0, 92% 100%, 0 100%);

            &.removing {
                animation: removingnRight 0.4s ease-in-out forwards;
            }
        }

        &__nav {
            background-color: transparent;
            border: none;
            transform-origin: top right;

            width: auto;
            height: auto;

            overflow: hidden;

            top: 92%;
            right: 60px;
            left: auto;
            transform: translate(0, 100%) rotate(90deg);
        }

        &__nav-link {
            font-size: 18px;

            transition: color 0.2s;

            padding: 12px 15px;

            border-radius: 25px;

            transition: color 0.2s, background 0.2s;

            &:hover,
            &:focus {
                color: @color-green;
            }

            &.active {
                color: #fff;
                background-color: @color-green-light;
            }
        }

        &__box {
            padding-top: 0;

            &--first {
                padding-left: 100px;

                padding-right: 130px;
            }

            &--second {
                padding-left: 130px;

                padding-right: 100px;

                & .tab__nav {
                    transform-origin: top left;
                    right: auto;
                    left: 90px;
                    top: 10%;
                }

                & .caworking {
                    & .section__bg-text {
                        right: auto;
                        left: -200px;
                    }
                }

                & .footer {
                    margin-left: -130px;
                    margin-right: -100px;
                    padding-right: 100px;
                }
            }
        }
    }

    @keyframes toLeft {
        from {
            transform: translateX(100%);
            opacity: 0;
        }

        50% {
            opacity: 1;
        }

        to {
            transform: translateX(5vw);
        }
    }

    @keyframes removingnLeft {
        from {
            transform: translateX(5vw);
        }

        50% {
            opacity: 1;
        }

        to {
            transform: translateX(100%);
            opacity: 0;
        }
    }

    @keyframes toRight {
        from {
            transform: translateX(-100%);
            opacity: 0;
        }

        50% {
            opacity: 1;
        }

        to {
            transform: translateX(-1vw);
        }
    }

    @keyframes removingnRight {
        from {
            transform: translateX(-1vw);
        }

        50% {
            opacity: 1;
        }

        to {
            transform: translateX(-100%);
            opacity: 0;
        }
    }
}

@media (min-width: @desktop-width) {
    .tab {

        width: 70vw;

        &.to-left {
            clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
        }

        &.to-right {
            clip-path: polygon(0 0, 100% 0, 90% 100%, 0 100%);
        }

        &__box {
            &--first {
                padding-left: 120px;

                & .footer {
                    margin-left: -130px;
                }
            }

            &--second {
                & .footer {
                    padding-right: 150px;
                }
            }
        }
    }

    @keyframes toLeft {
        from {
            transform: translateX(100%);
            opacity: 0;
        }

        50% {
            opacity: 1;
        }

        to {
            transform: translateX(30vw);
        }
    }

    @keyframes removingnLeft {
        from {
            transform: translateX(30vw);
        }

        50% {
            opacity: 1;
        }

        to {
            transform: translateX(100%);
            opacity: 0;
        }
    }

    @keyframes toRight {
        from {
            transform: translateX(-100%);
            opacity: 0;
        }

        50% {
            opacity: 1;
        }

        to {
            transform: translateX(-0vw);
        }
    }

    @keyframes removingnRight {
        from {
            transform: translateX(-10%);
        }

        50% {
            opacity: 1;
        }

        to {
            transform: translateX(-100%);
            opacity: 0;
        }
    }
}