.advantages {
    & .section__list {
        margin-bottom: -20px;

        &.flex {
            & .section__item {
                width: 95px;
                height: 103px;
                margin-bottom: 20px;
            }
        }
    }
}

@media (min-width: @laptop-width) {
    .advantages {
        & .section__list {
            margin-bottom: -65px;

            &.flex {
                & .section__item {
                    width: 140px;
                    height: 135px;

                    margin: 0 10px;
                    margin-bottom: 65px;
                }
            }
        }

        & .section__bg-text {
            right: auto;
            left: 60px;
            bottom: auto;
            top: 70%;
            transform: rotate(-90deg);

            font-size: 100px;

            transform-origin: bottom left;
        }
    }
}