.hall {
    &__image-box {
        position: relative;
        margin-bottom: 5px;
    }

    &__image {
        position: relative;
        width: 100%;
    }

    &__image-button {
        padding: 0;
        border: none;
        background-color: transparent;

        display: block;
        position: absolute;
        z-index: 2;

        width: 20px;
        height: 20px;

        color: #fff;

        border-radius: 50%;
        border: 2px solid #fff;

        padding-top: 1px;

        &--1 {
            bottom: 20%;
            right: 40%;
        }

        &--2 {
            top: 14%;
            right: 40%;
        }

        &--3 {
            top: 15%;
            right: 3%;
        }

        &--4 {
            left: 20%;
            top: 14%;
        }
    }

    &__tab {
        margin-bottom: 6px;
    }

    &__tab-button {
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        width: 100%;
        height: 65px;

        font-weight: 300;

        background-color: #fff;
        border: 1px solid @color-green-light;

        cursor: pointer;

        &.active {
            color: #fff;

            background-color: @color-green-light;

            & .hall__tab-button-name {
                color: #fff;
            }

            &+.hall__tab-content {
                display: block;
            }
        }
    }

    &__tab-content {
        display: none;
    }

    &__tab-button-name {
        display: block;

        color: @color-green-light;
        font-size: 24px;
        font-weight: 500;
    }

    &__text {
        margin: 0;
        border: 1px solid @color-green-light;
        border-top: none;

        padding: 11px 15px;
    }

    &__tab-content-box {
        border: 1px solid @color-green-light;
        border-top: none;
        padding: 3px;
    }
}

@media (min-width: @laptop-width) {
    .hall {
        &__image-box {
            margin-bottom: 30px;
        }

        &__image-button {
            width: 58px;
            height: 58px;

            font-size: 34px;
            font-weight: 500;

            border-width: 3px;

            outline: none;

            transition: background 0.2s, color 0.2s;

            cursor: pointer;

            &:hover,
            &:focus,
            &.active {
                background-color: #fff;
                color: @color-green;
            }
        }

        &__tab-button {
            text-align: left;
            align-items: flex-start;

            font-size: 40px;
            font-weight: 300;

            height: auto;

            border: none;

            cursor: default;

            outline: none;

            &.active {
                background-color: #fff;
                color: @color-black;

                & .hall__tab-button-name {
                    color: @color-green-light;
                }
            }
        }

        &__tab-button-name {
            font-size: 48px;
            line-height: 1;
        }

        &__tab-button-text {
            position: relative;

            padding-bottom: 15px;
            margin-bottom: 30px;

            &::before {
                content: "";
                position: absolute;
                display: block;

                bottom: 0;
                left: 0;
                right: 0;

                height: 1px;

                background-color: @color-green-light;
            }
        }

        &__text {
            border: none;

            font-size: 18px;

            padding: 0;
            margin-bottom: 35px;
        }

        &__tab {
            display: none;

            &.active {
                display: block;

                & .hall__tab-content {
                    display: block;
                }
            }
        }

        & .section__bg-text {
            right: -50px;
        }
    }
}