.social {
    padding: 0;
    margin: 0;
    list-style: none;

    display: flex;

    &__item {
        margin-right: 11px;
    }

    &__link {
        display: block;

        & .icon {
            fill: @color-green-light;
        }

        &:hover,
        &:focus {
            & .icon {
                fill: @color-green-dark;
            }
        }
    }
}