.gallery {
    &__list {
        margin: 0 auto;
        padding: 0;
        list-style: none;
    }

    &__item {
        width: 100%;
        max-width: 280px;

        background: #FFFFFF;
        box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.2);

        margin-bottom: 10px;
    }

    &__image-box {
        display: block;
        width: 100%;
        height: 284px;
        padding: 1px;
    }

    &__image {
        display: block;
        width: 100%;
        height: 100%;

        object-fit: cover;
    }

    &__item-text {
        margin: 0;
        padding: 6px 13px;

        font-size: 11px;
        color: #000;
        font-weight: 300;

        & span {
            display: block;
        }
    }
}

@media (min-width: @laptop-width) {
    .gallery {
        &__list {
            margin: 0 auto;
        }

        &__item {
            margin: 0;
            max-width: 100%;
            width: 280px;

            margin-bottom: 20px;

            & .gallery__image-box {
                height: 315px;
            }

            &:nth-child(2n) {
                & .gallery__image-box {
                    height: 245px;
                }
            }
        }

        &__item-text {
            font-size: 16px;
            color: @color-black;
            font-weight: 500;
        }
    }
}