.popup {
    display: none;
    position: fixed;
    z-index: 999;

    width: 100vw;
    height: 100vh;

    top: 0;
    left: 0;

    &.active {
        display: block;

        & .popup {

            &__close {
                animation: popupBoxShow 0.2s ease-in-out forwards;
            }

            &__box {
                animation: popupShow 0.3s ease-in-out forwards;
            }
        }

        &.removing {
            & .popup {
                &__close {
                    animation: popupBoxHide 0.3s ease-in-out forwards;
                }

                &__box {
                    animation: popupHide 0.3s ease-in-out forwards;
                }
            }
        }
    }

    &__close {
        padding: 0;
        border: none;
        background-color: transparent;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;

        top: 0;
        right: 0;

        width: 100%;
        height: 100%;

        background-color: rgba(0, 0, 0, 0.7);

        padding: 10px;

        box-sizing: border-box;
    }

    &__box {
        position: relative;
        width: 100%;
        max-width: 900px;
        height: 500px;

        background-color: #fff;

        & .button-close {
            position: absolute;

            top: -40px;
            right: 0;
        }
    }
}

@keyframes popupShow {
    from {
        transform: scale(0.7);
    }

    to {
        transform: scale(1);
    }
}

@keyframes popupHide {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(0.7);
    }
}

@keyframes popupBoxShow {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes popupBoxHide {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}