.main {
    position: relative;
    display: flex;
    flex-direction: column;

    width: 100vw;
    height: 100vh;

    overflow: hidden;

    padding-top: 54px;

    box-sizing: border-box;

    &--inner {
        height: auto;

        & .main__title {
            position: relative;
            margin: 0;
            width: 100%;

            color: @color-green-dark;
            font-size: 20px;
            text-align: right;
            font-weight: 400;

            padding: 0 13px;
            margin-top: 6px;

            box-sizing: border-box;

            &::before {
                content: "";
                position: absolute;
                display: block;

                width: 92px;
                height: 2px;

                top: 50%;
                left: 20px;
                transform: translateY(-50%);

                background-color: @color-green-dark;
            }
        }
    }

    &__block {
        position: relative;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        height: 100%;

        background-color: #fff;

        transition: all 0.4s ease-in-out;

        &::after {
            content: "";
            position: absolute;
            z-index: -1;
            opacity: 1;

            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;

            transition: opacity 0.4s 0.4s;
        }

        &--first {
            clip-path: polygon(0 0, 100% 0, 100% 90%, 0 100%);

            margin-bottom: -5%;

            &::after {
                background-image: url("../images/pictures/bg_main-second.jpg");
            }
        }

        &--second {
            clip-path: polygon(0 10%, 100% 0, 100% 100%, 0 100%);

            margin-top: -5%;

            &::after {
                background-image: url("../images/pictures/bg_main-first.jpg");
            }

            &::before {
                content: "";
                position: absolute;
                display: block;

                top: 0;
                left: 0;
                right: 0;

                height: 1px;

                transform: rotate(-5deg);
                transform-origin: top right;

                // background-color: @color-gray-intensiv;
            }
        }


        &.active {
            clip-path: none;
            flex-grow: 0;
            position: absolute;
            z-index: 3;

            top: 54px;
            left: 0;
            height: 78px;
            width: 100%;

            margin: 0;

            &::after {
                opacity: 0;

                transition: opacity 0s 0s;
            }

            &::before {
                display: none;
            }

            & .main__title {
                margin-bottom: 2px;
            }

            & .main__button-box {
                display: flex;

                & .button {
                    min-width: 95px;
                    margin: 0;
                    padding: 5px;

                    &:not(:last-child) {
                        margin-right: 10px;
                    }
                }
            }
        }
    }

    &__title {
        margin: 0;

        font-size: 30px;
        color: @color-black;
        text-align: center;

        margin-bottom: 15px;
    }

    &__button-box {
        & .button {
            margin: 0 auto;

            &:not(:last-child) {
                margin-bottom: 7px;
            }
        }
    }

    &__block-button-arrow {
        display: none;
    }

    &__section-title {
        margin: 0;

        font-size: 40px;
        color: @color-black;

        margin-bottom: 15px;
    }
}

@media (min-width: @laptop-width) {
    .main {
        display: flex;
        justify-content: space-between;

        padding: 0;
        flex-direction: row;

        background-image: url("../images/pictures/bg_main.jpg");
        background-size: cover;
        background-position: center;

        &::before {
            content: "";
            position: absolute;
            z-index: 1;

            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            background-color: rgba(0, 0, 0, 0.25);
        }

        &--inner {
            position: relative;
            flex-direction: column;

            background: #fff;

            padding-top: 110px;

            &::before {
                display: none;
            }

            & .main {
                &__title {
                    position: absolute;
                    font-size: 250px;
                    font-weight: 700;

                    top: -600px;
                    left: 300px;
                    transform: rotate(90deg);
                    transform-origin: top left;

                    opacity: 0.3;

                    &::before {
                        display: none;
                    }
                }
            }

            & .section {
                display: flex;
                flex-direction: column;
                align-items: center;
                padding: 0 100px;
                padding-top: 30px;
            }

            & .news__list {
                display: block;
                justify-content: flex-start;
                margin: 0 auto;
            }
        }

        &__title-bg {
            user-select: none;
            margin: 0;
            position: absolute;
            display: flex;
            flex-direction: column;
            z-index: 2;

            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            color: rgba(255, 255, 255, 0.5);
            font-size: 37vh;
            line-height: 0.8;
            font-weight: 500;
            text-transform: uppercase;
        }

        &__block {
            position: relative;
            width: 35%;
            min-width: 500px;
            margin: 0;
            z-index: 3;

            padding: 0 64px;

            box-sizing: border-box;

            &::after {
                opacity: 0;
            }

            &--first {
                clip-path: polygon(0 0, 100% 0, 80% 100%, 0 100%);

                padding-right: 100px;

                &.active {
                    clip-path: polygon(0 0, 100% 0, 80% 100%, 0 100%);

                    transform: translateX(-70%);
                }

                & .main {

                    &__title,
                    &__block-text {
                        text-align: left;
                    }

                    &__button-box {
                        justify-content: flex-start;
                    }

                    &__block-button-arrow {
                        right: 40px;
                        background-image: url("../images/icons/icon_arrow-right.svg");
                    }
                }
            }

            &--second {
                clip-path: polygon(20% 0, 100% 0, 100% 100%, 0 100%);

                padding-left: 100px;

                &.active {
                    clip-path: polygon(20% 0, 100% 0, 100% 100%, 0 100%);

                    transform: translateX(70%);
                }

                & .main {

                    &__title,
                    &__block-text {
                        text-align: right;
                    }

                    &__button-box {
                        justify-content: flex-end;
                    }

                    &__block-button-arrow {
                        left: 40px;
                        order: -1;
                        background-image: url("../images/icons/icon_arrow-left.svg");
                    }
                }
            }

            &.active {
                position: relative;

                top: auto;
                left: auto;

                height: 100%;
                width: 33%;

                &::after {
                    opacity: 1;
                }

                & .main {
                    &__button-box {
                        & .button {
                            min-width: 140px;

                            padding: 12px;
                            padding-bottom: 13px;

                            &:not(:last-child) {
                                margin-right: 40px;
                            }
                        }
                    }

                    &__title {
                        margin-bottom: 20px;
                    }

                    &__block-wrapper {
                        opacity: 1;
                    }

                    &__block-button-arrow {
                        opacity: 1;
                        visibility: visible;

                        &:hover,
                        &:focus {
                            opacity: 0.8;
                        }
                    }
                }
            }
        }

        &__title {
            display: block;
            width: 100%;
            font-size: 50px;
            font-weight: 600;

            margin-bottom: 20px;
        }

        &__block-text {
            margin: 0;

            color: @color-black;
            font-size: 14px;

            margin-bottom: 50px;
        }

        &__button-box {
            display: flex;

            & .button {
                margin: 0;

                &:not(:last-child) {
                    margin: 0;
                    margin-right: 40px;
                }
            }
        }

        &__block-button-arrow {
            opacity: 0;
            visibility: hidden;

            padding: 0;
            border: none;
            background-color: transparent;
            position: absolute;

            display: block;

            width: 80px;
            min-width: 80px;
            height: 80px;

            background-repeat: no-repeat;
            background-size: 50px;
            background-position: center;

            outline: none;

            transition: opacity 0.2s;

            margin-top: -5px;

            cursor: pointer;
        }

        &__curator-box {
            position: absolute;
            opacity: 0;

            top: 20%;
            max-width: 80%;

            transition: opacity 0.3s;
        }
    }
}

@media (min-width: @desktop-width) {
    .main {
        &__title {
            font-size: 50px;
        }

        &__block {
            &.active {
                & .main__button-box {
                    & .button {
                        min-width: 180px;
                    }
                }

                & .main__block-wrapper {
                    opacity: 1;
                }

                & .main__curator-box {
                    opacity: 1;
                }
            }

            &--first,
            &--second {
                &.active {
                    transform: none;
                }

                & .main__block-button-arrow {
                    position: relative;
                    right: auto;
                    left: auto;
                }
            }
        }

        &__block-button-arrow {
            background-size: 35px;
        }

        &__title-box {
            position: relative;
            display: flex;
        }
    }
}