.post {
    &__image-box {
        width: 100%;
        height: 144px;
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        padding: 7px 16px;
    }

    &__title {
        margin: 0;

        color: @color-black;
        font-size: 18px;

        margin-bottom: 7px;
    }

    &__date {
        color: @color-green-dark;
        font-size: 12px;

        margin-bottom: 12px;
    }

    &__text {
        margin: 0;

        color: #000;
        font-size: 12px;
    }
}