.nav {
    color: #4c4c4c;

    & .button-burger {
        &.active {
            &+.nav__list {
                opacity: 1;
                visibility: visible;

                transition: opacity 0.2s, visibility 0s 0s;
            }
        }
    }

    &__list {
        opacity: 0;
        margin: 0;
        list-style: none;

        position: fixed;
        left: 0;
        right: 0;
        top: 54px;

        padding: 7px 13px;

        background-color: #fff;

        transition: opacity 0.2s, visibility 0s 0.2s;

        visibility: hidden;
    }

    &__item {
        margin-bottom: 7px;
    }

    &__link {
        position: relative;
        display: block;

        width: 100%;
        color: inherit;
        text-decoration: none;
        font-size: 20px;
        text-align: right;

        &:hover,
        &:focus {
            color: @color-green;
        }

        &::before {
            content: "";
            position: absolute;
            display: block;

            width: 0;
            height: 2px;

            top: 50%;
            left: 20px;
            transform: translateY(-50%);

            background-color: @color-black;
        }

        &.active {
            color: @color-green;

            &::before {
                width: 92px;

                background-color: @color-green;
            }
        }
    }
}

@media (min-width: @laptop-width) {
    .nav {
        &__list {
            display: flex;
            position: relative;
            opacity: 1;
            visibility: visible;
            left: auto;
            right: auto;
            top: auto;
            padding: 0;
            background-color: transparent;
        }

        &__item {
            margin-bottom: 0;
            margin-right: 30px;
        }

        &__link {
            font-size: 18px;
            font-weight: 500;

            transition: color 0.2s;

            &:hover,
            &:focus {
                &::before {
                    width: 44px;
                    background-color: @color-green;
                }
            }

            &::before {
                width: 0;

                top: auto;
                bottom: -5px;
                left: 0;

                height: 2px;

                transition: width 0.2s, bacground-color 0.2s;
            }

            &.active {
                &::before {
                    width: 44px;
                }
            }
        }
    }
}

@media (min-width: @desktop-width) {
    .nav {
        &__item {
            margin-right: 60px;
        }
    }
}