.contact-page {
    &__map-box {
        width: 100%;
        height: 144px;

        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);

        margin-top: 7px;
    }

    &__content {
        padding: 15px 19px;
    }

    &__box {
        margin-bottom: 8px;
    }

    &__name {
        display: block;
        color: @color-black;
        font-size: 12px;
        font-weight: 600;
        margin-bottom: 2px;
    }

    &__link {
        display: block;
        color: @color-green-light;
        font-size: 14px;
        font-weight: 300;
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;

        margin-top: 2px;
    }

    &__item {
        position: relative;
        font-weight: 300;
        padding-left: 4px;

        &::before {
            content: "";
            display: inline-block;
            position: absolute;

            width: 2px;
            height: 2px;

            left: 0;
            top: 60%;
            transform: translateY(-50%);

            border-radius: 50%;
            background-color: @color-green-light;
        }
    }

    &__buttons {
        margin-top: 12px;

        & .button {
            min-width: 0;
            width: 95px;
            font-size: 10px;

            padding: 7px 2px;

            margin-right: 11px;
            margin-bottom: 12px;
        }
    }
}