.news {
    &__list {
        margin: 0 -9px;
        padding: 0;
        list-style: none;
    }

    &__item {
        display: flex;
        flex-grow: 1;

        width: 283px;
        height: 138px;

        background: #FFFFFF;
        box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.2);

        margin: 0 9px;
        margin-bottom: 9px;

        & .button {
            font-size: 10px;
            min-width: 0;
            padding: 5px 19px;
            padding-bottom: 6px;
            margin-right: auto;
        }
    }

    &__image-box {
        flex-grow: 1;

        width: 107px;
        min-width: 107px;
        height: 100%;
    }

    &__image {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item-content {
        display: flex;
        flex-direction: column;
        padding: 8px 9px;
    }

    &__item-title {
        margin: 0;

        color: @color-black;
        font-size: 12px;

        margin-bottom: 3px;
    }

    &__date {
        color: @color-green-light;
        font-size: 9px;

        margin-bottom: 5px;
    }

    &__text {
        margin: 0;

        color: #000;
        font-size: 9px;

        height: 58px;

        overflow: hidden;

        margin-bottom: 10px;
    }
}

@media (min-width: @laptop-width) {
    .news {
        &__list {
            justify-content: center;

            margin: 0 -25px;
        }

        &__item {
            position: relative;
            flex-grow: 0;
            flex-direction: column;

            width: 280px;
            height: 390px;

            margin: 0 25px;
            margin-bottom: 25px;

            overflow: hidden;

            &::before {
                content: "";
                position: absolute;
                display: block;

                width: 100%;
                height: 4px;

                top: 0;
                left: 0;
                transform: translateY(-4px);

                background-color: @color-green-dark;

                transition: transform 0.2s;
            }

            &:hover {
                box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

                &::before {
                    transform: translateY(0);
                }
            }

            & .button {
                margin: 0 auto;

                padding: 12px;
                padding-bottom: 13px;
                font-size: 18px;

                min-width: 180px;
            }
        }

        &__image-box {
            display: block;
            flex-grow: 0;

            width: 100%;
            height: 150px;

            margin-bottom: 0;
        }

        &__item-content {
            flex-grow: 1;

            padding: 20px 30px;
            padding-bottom: 25px;
        }

        &__item-title {
            display: none;
        }

        &__date {
            font-size: 12px;
        }

        &__text {
            font-size: 12px;

            height: 105px;

            margin-bottom: auto;
        }
    }
}