.section {
    padding: 11px 14px;

    &__list {
        padding: 0;
        margin: 0 -14px;
        list-style: none;

        &.flex {
            & .section {
                &__item {
                    width: 88px;
                    height: 88px;
                    margin: 0 5px;
                    margin-bottom: 5px;
                }
            }
        }
    }

    &__list-link {
        display: block;
        width: 100%;
        height: 100%;
    }

    &__list-image {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    &__title {
        margin: 0;

        color: @color-green-dark;
        font-size: 14px;
        font-weight: 500;

        margin-bottom: 8px;
    }

    &__text {
        margin: 0;
        font-size: 12px;
        color: #000;

        margin-bottom: 7px;
    }

    &__images {
        display: block;

        width: 100%;
        height: 100%;
        object-fit: cover;

        margin-bottom: 7px;
    }

    &__advantage-icon {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 65px;
        height: 65px;

        border-radius: 50%;
        background-color: @color-green-light;

        margin: 0 auto;
        margin-bottom: 10px;
    }

    &__item-text {
        margin: 0;
        text-align: center;
        font-size: 12px;
    }
}

@media (min-width: @laptop-width) {
    .section {
        position: relative;
        min-height: calc(100vh - 110px);

        padding: 11px 45px;
        padding-top: 110px;

        &__images {
            height: 330px;
            margin-bottom: 40px;
        }

        &__text {
            position: relative;
            font-size: 18px;

            padding-bottom: 18px;
            margin-bottom: 18px;

            &::before {
                content: "";
                position: absolute;
                display: block;

                width: 82px;
                height: 4px;

                bottom: 0;

                background-color: @color-green;
            }
        }

        &__bg-text {
            position: absolute;
            opacity: 0.36;
            z-index: -1;

            bottom: -10px;
            right: -300px;

            color: #b8ded3;
            font-size: 200px;
            font-weight: 600;
            white-space: nowrap;

            user-select: none;
        }

        &__advantage-icon {
            width: 80px;
            height: 80px;

            & .icon {
                transform: scale(1.2);
                transform-origin: center
            }
        }

        &__item-text {
            font-size: 18px;
        }

        &__list {
            max-width: 900px;
            margin: 0 auto;

            &.flex {
                & .section__item {
                    width: 290px;
                    height: 290px;

                    margin: 0 15px;
                    margin-bottom: 15px;
                }
            }
        }
    }
}