.footer {
    position: relative;

    box-shadow: 0 0 10px 0 rgba(0, 0, 0, .2);

    padding: 13px 21px;

    background-color: #fff;

    &__contacts {
        margin-bottom: 15px;
    }

    &__logo-box {
        position: absolute;

        top: 12px;
        right: 31px;
    }
}

@media (min-width: @laptop-width) {
    .footer {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        position: relative;
        z-index: 9;

        margin-left: -100px;
        margin-right: -130px;

        padding: 24px 60px;
        padding-left: 190px;

        &__logo-box {
            right: auto;
            top: 50%;
            left: 45px;
            transform: translateY(-50%);
        }

        &__logo {
            width: 90px;
        }

        &__buttons {
            & .button {
                font-size: 12px;

                padding: 8px;
                padding-bottom: 9px;

                &:not(:last-child) {
                    margin-right: 15px;
                }
            }

            & .link {
                margin-top: 10px;
                width: 100%;
                text-decoration: underline;
                padding-left: 24px;

                box-sizing: border-box;
            }
        }

        &__right {
            text-align: right;

            & .footer__social {
                justify-content: flex-end;
            }
        }
    }
}